import {useLoadFunction} from "lazy-viewport-loader";
import { reuseFunctions } from "@/js/reuse-functions.js";
//import { renderShopCard } from "@/js/partials/render-shop-card.js";
import { initSwiper } from "@/js/partials/swiper-production.js";
import { initTooltip } from "@/js/partials/tooltip.js";
import { flipCards } from "@/js/pages/main/flip-card.js";
//import { initSwiperNews } from "@/js/partials/news-swiper.js";
//import {addNews} from "@/js/firebase/add-news.js";
import {renderOneNews} from "@/js/partials/render-news.js";

document.addEventListener("DOMContentLoaded", async () => {
  reuseFunctions();
  useLoadFunction(
      () => import("@/js/partials/render-shop-card.js"),
      '.main-products',
      [],
      {threshold:0,
      rootMargin:'200px'}
  )
  //await renderShopCard();
  initSwiper(".production__swiper-main");
  initTooltip();
  flipCards();

  useLoadFunction(
      () => import("@/js/firebase/add-news.js"),
      '.news',
      [],
      {threshold:0,
        rootMargin:'200px'}
  )
  //initSwiperNews();
  await renderOneNews();
 // await addNews();

});
